.demo-home {
  box-sizing: border-box;
  width: 100%;
  min-height: 100vh;
  padding: 1.96267rem 0.85333rem 0.85333rem;
  background: #ffffff;
}
.demo-home__title,
.demo-home__desc {
  padding-left: 0.68267rem;
  font-weight: normal;
  line-height: 1;
  -webkit-user-select: none;
          user-select: none;
}
.demo-home__title {
  margin: 0 0 0.68267rem;
  font-size: 1.36533rem;
}
.demo-home__image,
.demo-home__text {
  display: inline-block;
  vertical-align: middle;
}
.demo-home__image {
  width: 1.36533rem;
  height: 1.36533rem;
}
.demo-home__text {
  margin-left: 0.68267rem;
  font-weight: 500;
}
.demo-home__title .demo-home--small {
  font-size: 1.024rem;
}
.demo-home__desc {
  margin: 0 0 1.70667rem;
  color: rgba(69, 90, 100, 0.6);
  font-size: 0.59733rem;
}
