.demo-home-nav__title {
  margin: 1.024rem 0 0.34133rem 0.68267rem;
  color: rgba(69, 90, 100, 0.6);
  font-size: 0.59733rem;
}
.demo-home-nav__block {
  position: relative;
  display: -webkit-flex;
  display: flex;
  margin: 0 0 0.512rem;
  padding-left: 0.85333rem;
  color: #323233;
  font-weight: 500;
  font-size: 0.59733rem;
  line-height: 1.70667rem;
  background: #f7f8fa;
  border-radius: 4.224rem;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}
.demo-home-nav__block:hover {
  background: #eef0f4;
}
.demo-home-nav__block:active {
  background: #e4e8ee;
}
.demo-home-nav__icon {
  position: absolute !important;
  top: 50%;
  right: 0.68267rem;
  width: 0.68267rem;
  height: 0.68267rem;
  margin-top: -0.34133rem;
  color: #b6c3d2;
  font-weight: 900 !important;
}
